import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import { useDispatch, useSelector } from 'react-redux'
import classNames from 'classnames/bind'
import { useLocation, useHistory } from 'react-router'
import api from '@/api/api.js'
import Partners from '@/components/header/Partners'
import { dialog } from '@/components/dialog/Dialog.js'
import { floatToFixed } from '@/utils/util.js'
import { setSignOut } from '@/store/actions/sign.js'
import { toggleSideBar, toggleLoginDialog } from '@/store/actions/common.js'
import { filterBannerItem } from '@/utils/util'
import scss from './header.module.scss'
import { playGame } from '@/utils/gameUtil'

const cx = classNames.bind(scss)

function LoginBefore(props) {
  const location = useLocation()
  const { dispatch, history } = props

  function toRegister() {
    history.push('/register', { form: location.pathname })
  }

  return (
    <div className={cx('login-before')}>
      <span>还不是会员？</span>
      <a className={cx('login-before__try-link')} href="javascript:void (0);">免费试玩</a>
      <div className={cx('login-before__sign')}>
        <a onClick={toRegister} className={cx('login-before__sign-link')}
          href="javascript:void (0);">注册</a>
        <span>/</span>
        <a onClick={() => dispatch(toggleLoginDialog(true))} className={cx('login-before__sign-link')}
          href="javascript:void (0);">登录</a>
      </div>
    </div>
  )
}

function LoginAfter(props) {
  const { customer, balance, dispatch, history } = props

  function getLogout() {
    history.push('/home')
    api.logout().then(({ head, body }) => {
      if (head.errCode === '0000') {
        dispatch(setSignOut(body.info))
      }
    })
  }

  function signOut() {
    dialog({
      title: '提示',
      message: '尊敬的贵宾，您确定要退出当前账号吗？',
      buttons: [{
        type: 'hollow',
        text: '确定退出',
        callback: getLogout
      }, {
        type: 'solid',
        text: '再玩一会'
      }]
    })
  }

  return (
    <div className={cx('login-after')}>
      <button onClick={() => history.push('/deposit')} className={cx('login-after__button')}>
        <i className={cx('login-after__button-icon', 'deposit')}/>
        <span>存款</span>
      </button>
      <button onClick={() => history.push('/withdraw')} className={cx('login-after__button')}>
        <i className={cx('login-after__button-icon', 'withdraw')}/>
        <span>取款</span>
      </button>
      <div className={cx('login-after__user')}>
        <div className={cx('login-after__account')}>
          <p>{customer.loginName}</p>
          <p className={cx('login-after__balance')}>
            ￥<span>{floatToFixed(balance)}</span>
          </p>
        </div>
        <i className={cx('login-after__avatar', [customer.gender === 'F' ? 'female' : 'male'])}/>
        <i className={cx('login-after__icon-menu')}/>
        <div className={cx('login-after__menu')}>
          <a className={cx('login-after__menu-item')} href='javascript:void (0);'>
            <span>用户中心</span>
            <i className={cx('login-after__menu-icon', 'arrow')}/>
          </a>
          <a className={cx('login-after__menu-item')} href='javascript:void (0);'>
            <span>洗码</span>
            <i className={cx('login-after__menu-icon', 'arrow')}/>
          </a>
          <a onClick={signOut} className={cx('login-after__menu-item')} href='javascript:void (0);'>
            <span>退出</span>
            <i className={cx('login-after__menu-icon', 'logout')}/>
          </a>
        </div>
      </div>
    </div>
  )
}

function Nav() {
  const { pathname } = useLocation()
  const history = useHistory()
  const homeNav = [
    {
      name: '首页',
      path: '/home'
    }, {
      name: '真人娱乐',
      path: '/live'
    }, {
      name: '电子游艺',
      path: '/games'
    }, {
      name: '体育竞技',
      path: '/sports'
    }, {
      name: '彩票投注',
      path: '/lottery'
    }, {
      name: '真人棋牌',
      gameCode: 'A02064'
    }, {
      name: '全民俱乐部',
      path: '/club'
    }, {
      name: '凯发风采',
      path: '/mien'
    }, {
      name: '最新优惠',
      path: '/promotions'
    }
  ]
  const userCenterNav = [
    {
      name: '会员首页',
      path: '/'
    }, {
      name: '资金管理',
      path: '/'
    }, {
      name: '账号设置',
      path: '/'
    }, {
      name: '资金明细',
      path: '/'
    }, {
      name: '超级合伙人',
      path: '/'
    }
  ]
  const navList = pathname === '/myAccount' ? userCenterNav : homeNav

  function handleClick(item) {
    if (item.path) {
      history.push(item.path)
    } else if (item.gameCode) {
      playGame({ gameCode: item.gameCode })
    }
  }

  return (
    <ul className={cx('nav')}>
      {navList.map((item, index) => {
        return (
          <li onClick={() => handleClick(item)} className={cx('nav__item')} key={index}>
            <a className={cx('nav__link', { active: pathname === item.path })}>
              {item.name}
            </a>
          </li>
        )
      })}
    </ul>
  )
}

function Header() {
  const history = useHistory()
  const dispatch = useDispatch()
  const banner = useSelector(state => state.banner)
  const customer = useSelector(state => state.customer)
  const showSideBar = useSelector(state => state.showSideBar)
  const balance = useSelector(state => +state.balances.withdrawBal)
  const loginStatus = !!customer.loginName
  const [bgUrl, setBgUrl] = useState(null)
  const [showMiniLogo, setShowMiniLogo] = useState(false)
  const className = cx('header', 'normal', {
    'open-side-bar': showSideBar,
    'theme-0501': getIsShowTheme('05-01 00:00:00', '05-05 23:59:59')
  })

  useEffect(init, [])

  function init() {
    getBannerList()
    window.addEventListener('scroll', handelScroll)
    return () => window.removeEventListener('scroll', handelScroll)
  }

  function handelScroll() {
    setShowMiniLogo(window.scrollY > 100)
  }

  function getBannerList() {
    if (!banner.header) return
    let list = banner.header.slider_data
    const wms = window.OMConfig.wmsServer + '/'
    list = list.filter(item => {
      item.imgUrl = `${wms}${item.imgurl}`
      return filterBannerItem(item)
    })
    setBgUrl(list[0] && list[0].imgUrl)
  }

  function getIsShowTheme(begin = '', end = '', useNowYear) {
    const now = Date.now()
    const nowYear = new Date().getFullYear()
    if (useNowYear !== false) {
      begin = nowYear + '-' + begin.replace(/^\d{4}-/, '')
      end = nowYear + '-' + end.replace(/^\d{4}-/, '')
    }
    begin = new Date(begin.replace(/-/g, '/')).getTime()
    end = new Date(end.replace(/-/g, '/')).getTime()
    return begin <= now && now <= end
  }

  return (
    <header style={{ backgroundImage: bgUrl && `url(${bgUrl})` }} className={className}>
      <div className={cx('header__inner', 'header__inner--normal', 'clearfix')}>
        <div className={cx('header__left')}>
          <button onClick={() => dispatch(toggleSideBar())} className={cx('side-bar-switch')}>
            <i className={cx('side-bar-switch__icon')}/>
            <span>导航</span>
          </button>
          <Partners/>
        </div>
        <a onClick={() => history.push('/home')} className={cx('normal__logo', { mini: showMiniLogo })}
          href="javascript:void (0);"/>
        <div className={cx('header__right')}>
          <div className={cx('message')}>
            <i className={cx('message__icon')}/>
            <div className={cx('message__box')}>
              <h4 className={cx('message__header')}>消息</h4>
              <div className={cx('message__content')}>
                <div className={cx('message__no-news')}>
                  <i className={cx('message__icon-max')}/>
                  <h6>暂时没有新的消息</h6>
                </div>
              </div>
            </div>
          </div>
          {
            loginStatus
              ? <LoginAfter balance={balance} customer={customer} dispatch={dispatch} history={history}/>
              : <LoginBefore dispatch={dispatch} history={history}/>
          }
        </div>
      </div>
      <Nav/>
    </header>
  )
}

LoginAfter.propTypes = {
  dispatch: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
  customer: PropTypes.object.isRequired,
  balance: PropTypes.number.isRequired
}
LoginBefore.propTypes = {
  dispatch: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired
}

export default Header
