import React, {
  useState, useEffect
} from 'react'
import classNames from 'classnames/bind'
import { useSelector, useDispatch } from 'react-redux'
import QrCode from '@/components/qr_code/QrCode'
import { useHistory } from 'react-router'
import { toggleSideBar } from '@/store/actions/common.js'

import scss from './sidebar.module.scss'

const cx = classNames.bind(scss)

function SideBar() {
  const history = useHistory()
  const dispatch = useDispatch()
  const showSideBar = useSelector(state => state.showSideBar)
  const [showLanguage, setShowLanguage] = useState(false)
  const [exitDone, setExitDone] = useState(!showSideBar)
  const className = cx('side-bar', {
    open: showSideBar,
    hide: !showSideBar && exitDone
  })
  const liveGame = [
    {
      name: 'AG旗舰厅'
    }, {
      name: 'AG国际厅'
    }, {
      name: '波音厅'
    }, {
      name: '龙门电投厅'
    }, {
      name: 'eBET厅'
    }, {
      name: 'MG厅'
    }
  ]
  const games = [
    {
      name: '失落的神庙'
    }, {
      name: '捕蝇大赛'
    }, {
      name: '森林舞会'
    }, {
      name: '招财进宝'
    }, {
      name: '不朽情缘'
    }, {
      name: '年年有余'
    }, {
      name: '百搭777'
    }, {
      name: '篮球巨星'
    }, {
      name: '龙发'
    }, {
      name: '掠夺银行'
    }, {
      name: '猴子疯狂'
    }, {
      name: '彩牛闪电奇袭'
    }
  ]
  const sports = [
    {
      name: '沙巴体育'
    }, {
      name: 'AG国际体育'
    }
  ]
  const lottery = [
    {
      name: 'KENO'
    }, {
      name: '时时彩'
    }
  ]
  const other = [
    {
      name: 'VIP专区'
    }, {
      name: '全民俱乐部'
    }, {
      name: '超级合伙人'
    }, {
      name: '新手学堂'
    }, {
      name: '最新优惠'
    }
  ]

  useEffect(() => {
    const timer = setTimeout(() => {
      setExitDone(!showSideBar)
    }, 300)
    if (showSideBar) {
      document.body.style.overflow = 'hidden'
    }
    return () => clearTimeout(timer)
  }, [showSideBar])

  useEffect(() => {
    if (exitDone) document.body.style.overflow = null
  }, [exitDone])

  function toggleLanguage(e) {
    setShowLanguage(!showLanguage)
    e && e.stopPropagation()
  }

  function closeSideBar() {
    dispatch(toggleSideBar())
  }

  return (
    <div className={className}>
      <div className={cx('side-bar__inner')}>
        <div className={cx('side-bar__scroll')}>
          <div className={cx('side-bar__content')}>
            <div className={cx('header')}>
              <a onClick={toggleLanguage} className={cx('header__link')}
                href="javascript:void (0);">{showLanguage ? '返回' : '中文站'}</a>
              <a onClick={closeSideBar} className={cx('header__close')} href="javascript:void (0);">
                关<span className={cx('header__font-icon')}>×</span>
              </a>
            </div>
            <div className={cx('module')}>
              <h3 className={cx('module__title')}>
                <span>真人娱乐</span>
              </h3>
              <div className={cx('module__content')}>
                {liveGame.slice(0, 3).map((item, index) => (
                  <a key={index.toString()} href="javascript:void (0);">{item.name}</a>
                ))}
                <br/>
                {liveGame.slice(3).map((item, index) => (
                  <a key={index.toString()} href="javascript:void (0);">{item.name}</a>
                ))}
              </div>
            </div>
            <div className={cx('module')}>
              <h3 className={cx('module__title')}>
                <span>电子游艺</span>
                <a onClick={() => history.push('/games')} className={cx('module__title-link')}
                  href="javascript:void (0);">更多</a>
              </h3>
              <div className={cx('module__content')}>
                {games.slice(0, 4).map((item, index) => (
                  <a key={index.toString()} href="javascript:void (0);">{item.name}</a>
                ))}
                <br/>
                {games.slice(4, 8).map((item, index) => (
                  <a key={index.toString()} href="javascript:void (0);">{item.name}</a>
                ))}
                <br/>
                {games.slice(8, 12).map((item, index) => (
                  <a key={index.toString()} href="javascript:void (0);">{item.name}</a>
                ))}
              </div>
            </div>
            <div className={cx('module')}>
              <h3 className={cx('module__title')}>
                <span>体育竞技</span>
              </h3>
              <div className={cx('module__content')}>
                {sports.map((item, index) => (
                  <a key={index.toString()} href="javascript:void (0);">{item.name}</a>
                ))}
              </div>
            </div>
            <div className={cx('module')}>
              <h3 className={cx('module__title')}>
                <span>彩票投注</span>
              </h3>
              <div className={cx('module__content')}>
                {lottery.map((item, index) => (
                  <a key={index.toString()} href="javascript:void (0);">{item.name}</a>
                ))}
              </div>
            </div>
            <div className={cx('module')}>
              <h3 className={cx('module__title')}>
                <span>其他</span>
              </h3>
              <div className={cx('module__content')}>
                {other.slice(0, 3).map((item, index) => (
                  <a key={index.toString()} href="javascript:void (0);">{item.name}</a>
                ))}
                <br/>
                {other.slice(3).map((item, index) => (
                  <a key={index.toString()} href="javascript:void (0);">{item.name}</a>
                ))}
              </div>
            </div>
            <div className={cx('download')}>
              <p className={cx('download__title')}>扫描二维码下载凯发app</p>
              <QrCode className={cx('download__qr-code')}/>
            </div>
            <div className={cx('language', { open: showLanguage })}>
              <div className={cx('module')}>
                <h3 className={cx('module__title')}>
                  <span>中文站</span>
                  <i className={cx('module__icon-checked')}/>
                </h3>
              </div>
              <div className={cx('module')}>
                <h3 className={cx('module__title')}>
                  <span>越南站</span>
                </h3>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div onClick={closeSideBar} className={scss['side-bar__mask']}/>
    </div>
  )
}

export default SideBar
