import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { useHistory } from 'react-router'
import classNames from 'classnames/bind'
import api from '@/api/api.js'
import Partners from '@/components/header/Partners'
import { setSignOut } from '@/store/actions/sign.js'
import { dialog } from '@/components/dialog/Dialog.js'
import { toggleLoginDialog } from '@/store/actions/common'
import scss from './header.module.scss'

const cx = classNames.bind(scss)

function HeaderPromo() {
  const history = useHistory()
  const dispatch = useDispatch()
  const loginName = useSelector(state => state.customer.loginName)

  function getLogout() {
    api.logout().then(({ head, body }) => {
      if (head.errCode === '0000') {
        dispatch(setSignOut(body.info))
      }
    })
  }

  function signOut() {
    dialog({
      title: '提示',
      text: '尊敬的贵宾，您确定要退出当前账号吗？',
      buttons: [{
        type: 'hollow',
        text: '确定退出',
        callback: getLogout
      }, {
        type: 'solid',
        text: '再玩一会'
      }]
    })
  }

  return (
    <div className={cx('header', 'promo')}>
      <div className={cx('header__inner', 'header__inner--promo', 'clearfix')}>
        <div className={cx('header__left')}>
          <i onClick={() => history.push('/home')} className={cx('promo__logo')}/>
          <span className={cx('promo__logo-split-line')}/>
          <Partners/>
        </div>
        <div className={cx('header__right')}>
          {
            loginName
              ? <p className={cx('promo__greetings')}>
                <span>欢迎您，{loginName}</span>
                <span className={cx('promo__split-line')}>|</span>
                <a onClick={signOut} className={cx('promo__link')} href="javascript:void (0);">
                  退出
                </a>
              </p>
              : <p className={cx('promo__greetings')}>欢迎您，请先登录</p>
          }
          {
            !loginName && <button onClick={() => dispatch(toggleLoginDialog(true))}
              className={cx('promo__button', 'sign-in')}>登录</button>
          }
          {
            !loginName && <button className={cx('promo__button', 'sign-up')}>注册</button>
          }
          <button onClick={() => history.push('/home')} className={cx('promo__button', 'back')}>返回首页</button>
        </div>
      </div>
    </div>
  )
}

export default HeaderPromo
