import axios from 'axios'
import http from '@/api/http.js'
import { baseGateway } from '@/api/api_config.js'

const CancelToken = axios.CancelToken

// 获取AG捕鱼奖池
export function queryAgFishJackpot(params, headers) {
  headers = {
    ...headers,
    cancelToken: new CancelToken((c) => {
      queryAgFishJackpot.cancel = c
    })
  }
  return http.post(`${baseGateway}/jackpot/queryAgFishJackpot`, params, headers)
}

// 获取PT捕鱼奖池
export function queryPTJackpot(params, headers) {
  return http.post(`${baseGateway}/jackpot/queryPTJackpot`, params, headers)
}

export default {
  queryPTJackpot,
  queryAgFishJackpot
}
