import React, { useEffect, useState } from 'react'
import classNames from 'classnames/bind'
import QrCode from '@/components/qr_code/QrCode'
import { easeToTop } from '@/utils/util'
import scss from './suspension.module.scss'

const cx = classNames.bind(scss)

export default function Suspension() {
  const [openService, setOpenService] = useState(false)
  const [openQrCode, setOpenQrCode] = useState(false)
  const [showMiniLogo, setShowMiniLogo] = useState(false)
  const [showToTop, setShowToTop] = useState(false)

  useEffect(() => {
    function handel() {
      setShowMiniLogo(window.scrollY > 100)
      setShowToTop(window.scrollY > 800)
    }

    window.addEventListener('scroll', handel)
    return () => window.removeEventListener('scroll', handel)
  }, [])

  function scrollToTop() {
    setShowToTop(false)
    easeToTop(window.scrollY, 0, 5, (val) => {
      console.log(val)
      window.scrollTo(0, val)
    })
  }

  return (
    <div className={cx('suspension', { min: showMiniLogo })}>
      <i onClick={scrollToTop} className={cx('suspension__icon-top', { hidden: !showToTop })}/>
      <br/>
      <div className={cx('suspension__item')}>
        <i className={cx('suspension__icon-club')}/>
      </div>
      <br/>
      <div className={cx('suspension__item', {
        open: openService,
        'open-service': openService
      })}>
        <div onClick={() => setOpenService(true)} className={cx({ hide: openService })}>
          <div className={cx('suspension__item-inner', { hide: showMiniLogo })}>
            <span className="fl">联系客服</span>
            <i className={cx('suspension__icon-tel')}/>
          </div>
          <div className={cx('suspension__item-inner', { hide: !showMiniLogo })}>
            <i className={cx('suspension__icon-tel-max')}/>
          </div>
        </div>
        <div className={cx({ hide: !openService })}>
          <div className={cx('suspension__item-inner')}>
            <i className={cx('suspension__icon-service')}/>
            <span>在线客服</span>
          </div>
          <div className={cx('suspension__item-inner')}>
            <i className={cx('suspension__icon-phone')}/>
            <span>免费电话</span>
          </div>
          <div className={cx('suspension__item-inner')}>
            <i className={cx('suspension__icon-tel')}/>
            <span>400-1201-721</span>
          </div>
          <div onClick={() => setOpenService(false)} className={cx('suspension__item-inner', 'close')}>
            <button>×</button>
          </div>
        </div>
      </div>
      <br/>
      <div className={cx('suspension__item', {
        open: openQrCode,
        'open-qr-code': openQrCode
      })}>
        <div onClick={() => setOpenQrCode(true)} className={cx({ hide: openQrCode })}>
          {
            showMiniLogo
              ? <div className={cx('suspension__item-inner')}>
                <i className={cx('suspension__icon-qr-code')}/>
              </div>
              : <div className={cx('suspension__item-inner')}>
                <span className="fl">全新手机APP下载</span>
                <i className={cx('suspension__icon-download')}/>
              </div>
          }
        </div>
        <div className={cx({ hide: !openQrCode })}>
          <div className={cx('suspension__item-inner')}>
            <span>扫描二维码下载凯发APP</span>
          </div>
          <div className={cx('suspension__item-inner')}>
            <QrCode/>
          </div>
          <div onClick={() => setOpenQrCode(false)} className={cx('suspension__item-inner', 'close')}>
            <button>×</button>
          </div>
        </div>
      </div>
    </div>
  )
}
