import React, { useEffect } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import { useSelector } from 'react-redux'
import { Redirect } from 'react-router'
import { imgLazyInit } from '@/utils/util'
import Footer from '@/components/footer/Footer'
import FooterPromo from '@/components/footer/FooterPromo'
import scss from './page.module.scss'

const cx = classNames.bind(scss)

function PageContainer({ route, setHeaderType, ...props }) {
  const { Main, auth, path, headerType, fillHeight } = route
  const showSideBar = useSelector(state => state.showSideBar)
  const loginStatus = useSelector(state => !!state.customer.loginName)
  const _headerType = headerType === null || headerType === 'promo' ? headerType : 'normal'
  const pageClassName = cx('page__content', {
    height: fillHeight,
    'open-side-bar': showSideBar,
    'padding-top-promo': _headerType === 'promo',
    'padding-top-normal': _headerType === 'normal'
  })

  const ToHome = <Redirect to="/home"/>

  const Page = (
    <div className={pageClassName}>
      {Main && <Main {...props}/>}
      {
        _headerType ? _headerType === 'promo' ? <FooterPromo/> : <Footer/> : null
      }
    </div>
  )

  useEffect(init, [])

  function init() {
    if (loginStatus && path === '/register') {
      props.history.push('/home')
    }
    setHeaderType(_headerType)
    return imgLazyInit()
  }

  return auth && !loginStatus ? ToHome : Page
}

PageContainer.propTypes = {
  route: PropTypes.object.isRequired
}

function areEqual(prevProps, nextProps) {
  return prevProps.route.path === nextProps.route.path
}

export default React.memo(PageContainer, areEqual)
