import QRCode from 'qrcodejs2'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import React, { useEffect, useRef } from 'react'
import scss from './qrcode.module.scss'

const cx = classNames.bind(scss)

export default function QrCode(props) {
  const { params, showLogo, className } = props
  const qrEl = useRef(null)

  useEffect(createQRCode, [params])

  function createQRCode() {
    const _params = {
      text: 'https://m.k801.com/k8_app.html?domain=k801.com',
      width: 124 * 2,
      height: 124 * 2,
      colorDark: '#000',
      colorLight: '#fff',
      correctLevel: QRCode.CorrectLevel.H,
      ...params
    }
    const qrCode = new QRCode(qrEl.current, _params)
    return () => qrCode
  }

  return (
    <div ref={qrEl} className={cx('qr-code', className)}>
      {
        showLogo && <img className={cx('qr-code__logo')} src={require('@/assets/img/common/logo28x28.png')} alt=""/>
      }
    </div>
  )
}

QrCode.defaultProps = {
  showLogo: true
}

QrCode.propTypes = {
  params: PropTypes.object,
  showLogo: PropTypes.bool,
  className: PropTypes.string
}
