import api from '@/api/api'
import * as types from '@/store/actionTypes'
import { getUUId } from '@/utils/util'

export function setBannerData(data) {
  return {
    type: types.setBannerData,
    state: { banner: data }
  }
}

export function toggleSideBar() {
  return {
    type: types.toggleSideBar,
    callback(state) {
      return {
        ...state,
        showSideBar: !state.showSideBar
      }
    }
  }
}

export function toggleLoginDialog(isShow = false) {
  return {
    type: types.toggleLoginDialog,
    state: { showLoginDialog: isShow }
  }
}

export function setWebToken(token) {
  return {
    type: types.setWebToken,
    callback(state) {
      token = !state.token || token === null ? token : state.token
      return {
        ...state,
        token
      }
    }
  }
}

export function setGameStatus(status) {
  return {
    type: types.setGameStatus,
    state: { gameStatus: status }
  }
}

// 获取token
export function getWebToken() {
  const params = { _isNeedLoading: false }
  return (dispatch, getState) => {
    const { token } = getState()
    if (token) return token
    return api.getWebToken(params).then((token) => {
      dispatch(setWebToken(token))
      return token
    })
  }
}

// 获取游戏厅方状态
export function getGameStatus(params = {}) {
  return dispatch => {
    return api.getGameStatus(params).then(({ head, success, body }) => {
      if (success) {
        dispatch(setGameStatus(body))
      }
      dispatch(setGameStatus({}))
      return {
        head,
        success,
        body
      }
    })
  }
}

// 获取banner数据
export function getBannerData() {
  const url = '/_banner/banner.txt'
  const params = {
    params: {
      _: getUUId()
    }
  }
  return dispatch => {
    return api.getWmsStatic(url, params).then(({ status, data }) => {
      if (status === 200 && typeof data === 'object') {
        dispatch(setBannerData(data || {}))
      }
    })
  }
}

export default {
  toggleSideBar,
  toggleLoginDialog,
  setWebToken,
  setGameStatus,
  getWebToken,
  getGameStatus,
  getBannerData
}
