import React, { lazy, Suspense, useState } from 'react'
import { Switch, Route, Redirect } from 'react-router'
import { createBrowserHistory } from 'history'
import config from '@/config/config'

import home from '@/views/home/home'
import Header from '@/components/header/Header'
import HeaderPromo from '@/components/header/HeaderPromo'
import PageLoading from '@/components/loading/PageLoading'
import Suspension from '@/components/suspension/Suspension'
import PageContainer from '@/components/page_container/PageContainer'
import scss from '@/components/page_container/page.module.scss'

/**
 * @description 路由字段说明
 * headerType 头部类型 normal|promo 默认normal，不需要头部值设为null
 * fillHeight {Boolean} 组件父元素height是否设为100%，默认false
 * */
const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    Main: home
  },
  {
    path: '/live',
    Main: lazy(() => import('@/views/live/live'))
  },
  {
    path: '/games',
    Main: lazy(() => import('@/views/games/games'))
  },
  {
    path: '/sports',
    Main: lazy(() => import('@/views/sports/sports'))
  },
  {
    path: '/lottery',
    Main: lazy(() => import('@/views/lottery/lottery'))
  },
  {
    path: '/mien',
    Main: lazy(() => import('@/views/mien/mien'))
  },
  {
    path: '/promotions',
    Main: lazy(() => import('@/views/promotions/promotions'))
  },
  {
    path: '/register',
    Main: lazy(() => import('@/views/register/register'))
  },
  // {
  //   path: '/club',
  //   fillHeight: true,
  //   Main: lazy(() => import('@/views/club/club'))
  // },
  {
    path: '/loadingGame',
    headerType: null,
    fillHeight: true,
    Main: lazy(() => import('@/views/common/loadingGame'))
  }
]

routes.push({
  path: '*',
  headerType: null,
  fillHeight: true,
  Main: lazy(() => import('@/views/common/error404'))
})

export const history = createBrowserHistory({
  basename: process.env.NODE_ENV !== 'development' ? config.outputDir : ''
})

export default function Routes() {
  const [headerType, setHeaderType] = useState('normal')

  function render(props, route) {
    return <PageContainer setHeaderType={setHeaderType} route={route} {...props}/>
  }

  return (
    <div className={scss.page}>
      {
        headerType ? headerType === 'promo' ? <HeaderPromo/> : <Header/> : null
      }
      <Suspense fallback={<PageLoading/>}>
        <Switch>
          {routes.map((route, index) => {
            return (
              route.redirect
                ? <Redirect
                  exact
                  strict
                  key={index}
                  from={route.path}
                  to={route.redirect}/>
                : <Route
                  key={index}
                  path={route.path}
                  strict={route.strict}
                  exact={route.exact !== false}
                  render={props => render(props, route)}
                />
            )
          })}
        </Switch>
      </Suspense>
      {
        headerType && headerType !== 'promo' && <Suspension/>
      }
    </div>
  )
}

history.listen((location, action) => {
  window.scrollTo(0, 0)
})
